.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  outline: none;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px;
  outline: none;
  width: 300px;
  margin-right: 10px;
  padding: 10px 15px;
  font-size: 16px;
  box-shadow: 0 2px 4px #0000001a;
}

.search-form input[type="text"]:focus {
  box-shadow: 0 0 8px #007bff99;
}

.search-form button[type="submit"], .load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
}

.search-form button[type="submit"]:hover, .load-more:hover {
  background-color: #0056b3;
}

